@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 400;
  font-display: swap;
  src: url(/static/media/Inter-Regular.2505c0d4.woff2) format("woff2"),
       url(/static/media/Inter-Regular.fc49d131.woff) format("woff");
}
@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 500;
  font-display: swap;
  src: url(/static/media/Inter-Medium.5b53c519.woff2) format("woff2"),
       url(/static/media/Inter-Medium.cc6a8868.woff) format("woff");
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 900;
  font-display: swap;
  src: url(/static/media/Inter-Black.23eb804e.woff2) format("woff2"),
       url(/static/media/Inter-Black.16792010.woff) format("woff");
}

.page__content {
    max-width: 1280px;
    margin: 0 auto;
}
.page {
    /*max-width: 1280px;*/
    background-color: #000000;
    font-family: 'Inter', ‘Arial’, sans-serif;
    /*margin: 0 auto;*/
}
.header__logo {
    width: 142px;
    padding-bottom: 41px;
}
@media (max-width: 850px) {
    .header__logo {
        width: 104px;
        padding-bottom: 31px;
    }
}
.header {
    padding: 45px 0 0;
    max-width: 69%;
    margin: 0 auto;
    border-bottom: 1px solid rgba(84, 84, 84, .7);
}

@media (max-width: 850px) {
    .header {
        max-width: 89.5%;
        padding-top: 28px;
    }
}
@media (max-width: 320px) {
    .header {
        max-width: 100%;
        padding-left: 27px;
    }
}
.content {
    max-width: 69%;
    margin: 0 auto;
}

@media (max-width: 850px) {
    .content {
        max-width: 89.5%;
    }
}
.profile__avatar {
    position: relative;
}
.profile__img {
    border-radius: 50%;
    max-width: 120px;
    width: 120px;
    height: 120px;
    object-fit: cover;
}
.profile__info {
    flex-basis: 60%;
}
.profile__name {
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 56px;
    color: #FFFFFF;
    margin: 0;
    padding-right: 17px;
    padding-bottom: 16px;
    max-width: 500px;
    box-sizing: border-box;
}

@media (max-width: 850px) {
    .profile__name {
        font-size: 27px;
        padding-right: 6px;
        padding-bottom: 14px;
        padding-top: 10px;
    }
}
@media (max-width: 570px) {
    .profile__name {
        text-align: center;
    }
}
.profile__job {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
    margin: 0;
    max-width: 500px;
    box-sizing: border-box;
}

@media (max-width: 850px) {
    .profile__job {
        font-size: 14px;
        padding-bottom: 36px;
        
    }
}
@media (max-width: 570px) {
    .profile__job {
        text-align: center;
    }
}
.profile__edit-button {
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    width: 24px;
    height: 24px;
    background-image: url(/static/media/pen.8667ac4a.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-color: rgba(0, 0, 0, 0);
}

.profile__edit-button:hover {
    opacity: .6;
    transition: 1s linear;
}
.profile__container {
    display: flex;
    align-items: baseline;
}
.profile__add-button {
    border: 2px solid #FFFFFF;
    border-radius: 2px;
    width: 150px;
    height: 50px;
    background-image: url(/static/media/plus.d86bb2ed.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-color: rgba(0, 0, 0, 0);
}

.profile__add-button:hover {
    opacity: .6;
    transition: 1s linear;
}

@media (max-width: 570px) {
    .profile__add-button {
        width: 100%;
    }
}

.profile__cover {
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0);
    width: 120px;
    height: 120px;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.profile__cover:hover {
    transition: .5s linear;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 50%;
}
.profile__button {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background-image: url(/static/media/vector-pen.a086f758.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    opacity: 0;
    outline: 0;
    cursor: pointer;
}

.profile__button:hover {
    transition: .5s linear;
    opacity: 1;
}
.profile {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 36px;
    padding-bottom: 47px;


    /* position: relative; */
}

@media (max-width: 570px) {
    .profile {
        flex-direction: column;
    }
}

.card__grid {
    background: #fff;
    border-radius: 10px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    position: relative;
}
.card__img {
    width: 100%;
    min-height: 282px;
    max-height: 282px;
    object-fit: cover;
    cursor: pointer;
}
.card__item {
    display: flex;
    justify-content: space-between;
    padding: 25px 15px 31px 21px;
}
.card__heading {
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
@media ) {
.card__like_active {
    background-image: url(/static/media/like_active.75438e94.svg);
}
}
/**/
.card__like {
    background-image: url(/static/media/vector-like.49570bcb.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    width: 28px;
    height: 28px;
    box-sizing: border-box;
    outline: 0;
}
.card__like:hover {
    opacity: .6;
    transition: 1s linear;
}
.card__delete {
    position: absolute;
    right: 16px;
    top: 18px;
    background-image: url(/static/media/trash.a274c87c.svg);
    background-position: center;
    background-repeat: no-repeat;
    border: rgba(0, 0, 0, 0);
    width: 18px;
    height: 18px;
    background-color: rgba(0, 0, 0, 0);
    outline: 0;
    display: none;
}
.card__delete:hover {
    opacity: .6;
    transition: 1s linear;
}
.card__counter {
    width: 9px;
    height: 13px;
    font-size: 13px;
    line-height: 16px;
    text-align: center;
    position: absolute;
    top: 23px;
    left: 9px;
}
.card__like-container {
    position: relative;
}
.card {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 18px;
    row-gap: 18px;
    grid-column-gap: 21px;
    -webkit-column-gap: 21px;
            column-gap: 21px;
    list-style-type: none;
    padding: 0;
    margin: 0;
}
@media (max-width: 570px) {
    .card {
        grid-template-columns: repeat(2, 1fr);
    }
}
@media (max-width: 380px) {
    .card {
        grid-template-columns: repeat(1, 1fr);
    }
}
.footer__copyright {
    color: #545454;
    margin: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
}

@media (max-width: 570px) {
    .footer__copyright {
        font-size: 14px;
        line-height: 17px;
    }
}
.footer {
    max-width: 69%;
    margin: 0 auto;
    padding: 66px 0 60px;
}

@media (max-width: 570px) {
    .footer {
    padding: 48px 0 36px;
    }
}

@media (max-width: 850px) {
    .footer {
        max-width: 89.5%;
    }
}
.popup__form {
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    width: 430px;
    display: flex;
    flex-direction: column; 
    margin: 0 auto;
    position: relative;
}

@media (max-width: 560px) {
    .popup__form {
        width: 240px;
    }
}
/*@import url(./_type_img/popup__close_type_img.css);*/

.popup__close {
    position: absolute;
    right: -41px;
    top: -38px;
    border: 0;
    font-size: 34px;
    background-image: url(/static/media/icon-close.0a2678e1.svg);
    background-position: 0%;
    background-repeat: no-repeat;
    background-color: rgba(0, 0, 0, 0);
    min-width: 41px;
    min-height: 41px;
    box-sizing: border-box;
    outline: 0;
}

.popup__close:hover {
opacity: .6;
}
.popup__title {
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    padding-top: 34px;
    line-height: 29px;
    margin: 0;
    width: 83%;
    margin: 0 auto;
    padding-bottom: 39px;
}
.popup__input {
    width: 83%;
    margin: 0 auto;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    height: 48px;
    font-size: 14px;
    line-height: 17px;
    outline: 0;
}
.popup__button {
    width: 83%;
    margin: 0 auto;
    height: 50.15px;
    background: #000000;
    border-radius: 2px;
    color: #fff;
    border: 0;
    margin-top: 43px;
    margin-bottom: 36px;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
}
.popup__button:hover {
    opacity: .8;
}
.popup__content {
    position: relative;
}
.popup__img {
    max-width: 75vw;
    max-height: 75vh;
}
.popup__img-title {
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    color: #FFFFFF;
    padding-top: 10px;
}
.popup {
    font-family: 'Inter', ‘Arial’, sans-serif;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100vw;
    display: flex;/*none*/
    align-items: center;
    justify-content: center;
    visibility: hidden;
    opacity: 0;
    transition: visibility .5s, opacity .5s linear;
}

.popup_open {
    opacity: 1;
    visibility: visible;
}
.card__like_active {
    background-image: url(/static/media/like_active.75438e94.svg);
}
.popup__close_type_img{
    right: -4px;
    top: -23px;
}
.popup__button_disabled {
    color: #C4C4C4;
    border: 1px solid #C4C4C4;;
    background: #fff;
}
.popup__input_type_error {
    border-bottom: 1px solid #FF0000;
}
.popup__error_visible {
    max-width: 358px;
    margin: 5px 0 0 36px;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    color: #FF0000;
}

@media (max-width: 570px) {
    .popup__error_visible {
        margin-left: 20px;
    }
}
.popup__title_type_deleting {
    padding-bottom: 0;
    margin-bottom: 1.5px;
}
.card__delete_type_hidden{
    display: block;
}


